"use client"

import { create } from "zustand"
import { persist } from "zustand/middleware"
import type {
  CRMRecord,
  ProtectRecord,
  SettlementRecord,
  UploadHistory,
  Remark,
  ActivityLogEntry,
  Status,
  DPDGroup,
  Partner,
} from "./types"

interface CRMStore {
  records: CRMRecord[]
  uploadHistory: UploadHistory[]

  // Filters
  searchQuery: string
  statusFilter: string
  partnerFilter: string
  dpdGroupFilter: string
  dateRangeStart: string
  dateRangeEnd: string
  typeFilter: "protect" | "settlement" | "both"
  dateFilterType: "today" | "7d" | "30d" | "1y" | "custom"

  // Actions
  setRecords: (records: CRMRecord[]) => void
  addRecords: (records: CRMRecord[], upload: UploadHistory) => void
  updateRecord: (id: string, updates: Partial<CRMRecord>) => void
  updateRecordStatus: (id: string, status: Status) => void
  updateRecordStage: (id: string, stage: string) => void
  addRemark: (recordId: string, remark: Omit<Remark, "id">) => void
  updateRemark: (recordId: string, remarkId: string, text: string) => void
  deleteRemark: (recordId: string, remarkId: string) => void
  addActivityLog: (recordId: string, entry: Omit<ActivityLogEntry, "id">) => void

  // Filter setters
  setSearchQuery: (query: string) => void
  setStatusFilter: (status: string) => void
  setPartnerFilter: (partner: string) => void
  setDpdGroupFilter: (group: string) => void
  setDateRange: (start: string, end: string) => void
  setTypeFilter: (type: "protect" | "settlement" | "both") => void
  setDateFilterType: (type: "today" | "7d" | "30d" | "1y" | "custom") => void

  // Getters
  getProtectRecords: () => ProtectRecord[]
  getSettlementRecords: () => SettlementRecord[]
  getFilteredProtectRecords: () => ProtectRecord[]
  getFilteredSettlementRecords: () => SettlementRecord[]
  getFilteredRecords: () => CRMRecord[]
  getRecordById: (id: string) => CRMRecord | undefined
}

// Helper to determine DPD Group
export function getDPDGroup(dpd: number): DPDGroup {
  if (dpd <= 30) return "0-30"
  if (dpd <= 60) return "31-60"
  if (dpd <= 90) return "61-90"
  if (dpd <= 180) return "91-180"
  return "180+"
}

// Helper to detect partner from data
export function detectPartner(row: Record<string, unknown>): Partner {
  const values = Object.values(row).map((v) => String(v).toLowerCase())
  if (values.some((v) => v.includes("sayyam"))) return "sayyam"
  if (values.some((v) => v.includes("snapmint"))) return "snapmint"
  // Default based on column patterns or random
  return Math.random() > 0.5 ? "sayyam" : "snapmint"
}

// Helper to detect record type from columns
export function detectRecordType(columns: string[]): "protect" | "settlement" {
  const lowerCols = columns.map((c) => c.toLowerCase())
  // Settlement has specific columns
  if (
    lowerCols.some(
      (c) =>
        c.includes("lender") || c.includes("credit card") || c.includes("legal notice") || c.includes("recommended"),
    )
  ) {
    return "settlement"
  }
  // Protect has EMI-related columns
  if (
    lowerCols.some((c) => c.includes("emi") || c.includes("dpd") || c.includes("plan") || c.includes("institution"))
  ) {
    return "protect"
  }
  return "protect"
}

export const useCRMStore = create<CRMStore>()(
  persist(
    (set, get) => ({
      records: [],
      uploadHistory: [],
      searchQuery: "",
      statusFilter: "all",
      partnerFilter: "all",
      dpdGroupFilter: "all",
      dateRangeStart: "",
      dateRangeEnd: "",
      typeFilter: "both",
      dateFilterType: "30d",

      setRecords: (records) => set({ records }),

      addRecords: (newRecords, upload) =>
        set((state) => ({
          records: [...state.records, ...newRecords],
          uploadHistory: [upload, ...state.uploadHistory],
        })),

      updateRecord: (id, updates) =>
        set((state) => ({
          records: state.records.map((r) => (r.id === id ? { ...r, ...updates } : r)),
        })),

      updateRecordStatus: (id, status) => {
        const record = get().records.find((r) => r.id === id)
        if (record) {
          const entry: Omit<ActivityLogEntry, "id"> = {
            action: "Status Changed",
            details: `Status updated to "${status}"`,
            timestamp: new Date().toISOString(),
            user: "Current User",
          }
          set((state) => ({
            records: state.records.map((r) => {
              if (r.id === id) {
                return { ...r, status, activityLog: [...r.activityLog, { ...entry, id: `log-${Date.now()}` }] } as CRMRecord
              }
              return r
            }),
          }))
        }
      },

      updateRecordStage: (id, stage) => {
        const record = get().records.find((r) => r.id === id)
        if (record) {
          const entry: Omit<ActivityLogEntry, "id"> = {
            action: "Stage Changed",
            details: `Stage updated to "${stage}"`,
            timestamp: new Date().toISOString(),
            user: "Current User",
          }
          set((state) => ({
            records: state.records.map((r) =>
              r.id === id ? { ...r, stage, activityLog: [...r.activityLog, { ...entry, id: `log-${Date.now()}` }] } : r,
            ),
          }))
        }
      },

      addRemark: (recordId, remark) =>
        set((state) => ({
          records: state.records.map((r) =>
            r.id === recordId
              ? {
                ...r,
                remarks: [...r.remarks, { ...remark, id: `remark-${Date.now()}` }],
                activityLog: [
                  ...r.activityLog,
                  {
                    id: `log-${Date.now()}`,
                    action: "Remark Added",
                    details: remark.text.substring(0, 50) + (remark.text.length > 50 ? "..." : ""),
                    timestamp: new Date().toISOString(),
                    user: remark.createdBy,
                  },
                ],
              }
              : r,
          ),
        })),

      updateRemark: (recordId, remarkId, text) =>
        set((state) => ({
          records: state.records.map((r) =>
            r.id === recordId
              ? {
                ...r,
                remarks: r.remarks.map((rem) =>
                  rem.id === remarkId ? { ...rem, text, updatedAt: new Date().toISOString() } : rem,
                ),
              }
              : r,
          ),
        })),

      deleteRemark: (recordId, remarkId) =>
        set((state) => ({
          records: state.records.map((r) =>
            r.id === recordId ? { ...r, remarks: r.remarks.filter((rem) => rem.id !== remarkId) } : r,
          ),
        })),

      addActivityLog: (recordId, entry) =>
        set((state) => ({
          records: state.records.map((r) =>
            r.id === recordId ? { ...r, activityLog: [...r.activityLog, { ...entry, id: `log-${Date.now()}` }] } : r,
          ),
        })),

      setSearchQuery: (query) => set({ searchQuery: query }),
      setStatusFilter: (status) => set({ statusFilter: status }),
      setPartnerFilter: (partner) => set({ partnerFilter: partner }),
      setDpdGroupFilter: (group) => set({ dpdGroupFilter: group }),
      setDateRange: (start, end) => set({ dateRangeStart: start, dateRangeEnd: end }),
      setTypeFilter: (type) => set({ typeFilter: type }),
      setDateFilterType: (type) => set({ dateFilterType: type }),

      getProtectRecords: () => get().records.filter((r): r is ProtectRecord => r.type === "protect"),
      getSettlementRecords: () => get().records.filter((r): r is SettlementRecord => r.type === "settlement"),

      getFilteredProtectRecords: () => {
        const { records, searchQuery, statusFilter, partnerFilter, dpdGroupFilter, dateRangeStart, dateRangeEnd } =
          get()
        return records
          .filter((r): r is ProtectRecord => r.type === "protect")
          .filter((r) => {
            const matchesSearch =
              searchQuery === "" ||
              r.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
              r.mobileNumber.includes(searchQuery) ||
              r.accountNumber.includes(searchQuery)
            const matchesStatus = statusFilter === "all" || r.status === statusFilter
            const matchesPartner = partnerFilter === "all" || r.partner === partnerFilter
            const matchesDpd = dpdGroupFilter === "all" || r.dpdGroup === dpdGroupFilter

            let matchesDate = true
            if (dateRangeStart && dateRangeEnd) {
              const recordDate = new Date(r.formFilledDate)
              matchesDate = recordDate >= new Date(dateRangeStart) && recordDate <= new Date(dateRangeEnd)
            }

            return matchesSearch && matchesStatus && matchesPartner && matchesDpd && matchesDate
          })
      },

      getFilteredSettlementRecords: () => {
        const { records, searchQuery, statusFilter, partnerFilter, dateRangeStart, dateRangeEnd } = get()
        return records
          .filter((r): r is SettlementRecord => r.type === "settlement")
          .filter((r) => {
            const matchesSearch =
              searchQuery === "" ||
              r.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
              r.mobileNumber.includes(searchQuery) ||
              r.loanAccNo.includes(searchQuery)
            const matchesStatus = statusFilter === "all" || r.status === statusFilter
            const matchesPartner = partnerFilter === "all" || r.partner === partnerFilter

            let matchesDate = true
            if (dateRangeStart && dateRangeEnd) {
              const recordDate = new Date(r.formFilledDate)
              matchesDate = recordDate >= new Date(dateRangeStart) && recordDate <= new Date(dateRangeEnd)
            }

            return matchesSearch && matchesStatus && matchesPartner && matchesDate
          })
      },

      getFilteredRecords: () => {
        const { records, searchQuery, statusFilter, partnerFilter, dpdGroupFilter, dateRangeStart, dateRangeEnd, typeFilter } = get()

        return records.filter((r) => {
          // Type Filter
          if (typeFilter !== "both" && r.type !== typeFilter) return false

          // Search Filter
          const matchesSearch =
            searchQuery === "" ||
            r.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
            r.mobileNumber.includes(searchQuery) ||
            (r.type === "protect" ? (r as ProtectRecord).accountNumber.includes(searchQuery) : (r as SettlementRecord).loanAccNo.includes(searchQuery))

          // Common Filters
          const matchesStatus = statusFilter === "all" || r.status === statusFilter
          const matchesPartner = partnerFilter === "all" || r.partner === partnerFilter

          // DPD Filter (only for protect)
          const matchesDpd = r.type === "protect" ? (dpdGroupFilter === "all" || (r as ProtectRecord).dpdGroup === dpdGroupFilter) : true

          // Date Filter
          let matchesDate = true
          if (dateRangeStart && dateRangeEnd) {
            const recordDate = new Date(r.formFilledDate)
            matchesDate = recordDate >= new Date(dateRangeStart) && recordDate <= new Date(dateRangeEnd)
          }

          return matchesSearch && matchesStatus && matchesPartner && matchesDpd && matchesDate
        })
      },

      getRecordById: (id) => get().records.find((r) => r.id === id),
    }),
    {
      name: "crm-storage",
    },
  ),
)
