// Calculate dashboard stats
export function calculateDashboardStats(
  records: (ProtectRecord | SettlementRecord)[],
  uploadHistory: UploadHistory[],
): DashboardStats {
  const protectRecords = records.filter((r) => r.type === "protect") as ProtectRecord[]
  const settlementRecords = records.filter((r) => r.type === "settlement") as SettlementRecord[]

  // DPD distribution for protect records
  const dpdGroups: Record<string, number> = { "0-30": 0, "31-60": 0, "61-90": 0, "91-180": 0, "180+": 0 }
  protectRecords.forEach((r) => {
    dpdGroups[r.dpdGroup]++
  })

  // Daily requests for last 30 days
  const dailyMap = new Map<string, { count: number; protect: number; settlement: number }>()
  const today = new Date()
  for (let i = 29; i >= 0; i--) {
    const d = new Date(today)
    d.setDate(d.getDate() - i)
    const key = d.toISOString().split("T")[0]
    dailyMap.set(key, { count: 0, protect: 0, settlement: 0 })
  }

  records.forEach((r) => {
    const date = r.formFilledDate.split("T")[0]
    if (dailyMap.has(date)) {
      const entry = dailyMap.get(date)!
      entry.count++
      if (r.type === "protect") entry.protect++
      else entry.settlement++
    }
  })

  return {
    totalRequests: records.length,
    totalProtect: protectRecords.length,
    totalSettlement: settlementRecords.length,
    sayyamProtect: protectRecords.filter((r) => r.partner === "sayyam").length,
    sayyamSettlement: settlementRecords.filter((r) => r.partner === "sayyam").length,
    snapmintProtect: protectRecords.filter((r) => r.partner === "snapmint").length,
    snapmintSettlement: settlementRecords.filter((r) => r.partner === "snapmint").length,
    dpdDistribution: Object.entries(dpdGroups).map(([group, count]) => ({ group: group as any, count })),
    dailyRequests: Array.from(dailyMap.entries()).map(([date, data]) => ({ date, ...data })),
    recentUploads: uploadHistory.slice(0, 5),
    newRequestsToday: records.filter(r => r.formFilledDate.startsWith(new Date().toISOString().split('T')[0])).length,
  }
}
