"use client"

import { useState } from "react"
import type { CRMRecord, ProtectRecord, SettlementRecord, Status, Remark } from "@/lib/types"
import { useCRMStore } from "@/lib/store"
import { cn } from "@/lib/utils"
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Textarea } from "@/components/ui/textarea"
import {
  User,
  Phone,
  Building,
  Calendar,
  FileText,
  MessageSquare,
  History,
  Plus,
  Edit2,
  Trash2,
  Save,
  X,
} from "lucide-react"

const statusStyles: Record<Status, string> = {
  new: "bg-blue-500/10 text-blue-500 border-blue-500/20",
  "in-progress": "bg-yellow-500/10 text-yellow-500 border-yellow-500/20",
  "follow-up": "bg-purple-500/10 text-purple-500 border-purple-500/20",
  closed: "bg-green-500/10 text-green-500 border-green-500/20",
}

const stages = ["Initial Review", "Documentation", "Negotiation", "Verification", "Final Review", "Resolved"]

interface RecordDetailModalProps {
  record: CRMRecord | null
  open: boolean
  onClose: () => void
}

export function RecordDetailModal({ record, open, onClose }: RecordDetailModalProps) {
  const { updateRecordStatus, updateRecordStage, addRemark, updateRemark, deleteRemark } = useCRMStore()
  const [newRemark, setNewRemark] = useState("")
  const [editingRemarkId, setEditingRemarkId] = useState<string | null>(null)
  const [editingRemarkText, setEditingRemarkText] = useState("")

  if (!record) return null

  const isProtect = record.type === "protect"
  const protectRecord = record as ProtectRecord
  const settlementRecord = record as SettlementRecord

  const handleAddRemark = () => {
    if (!newRemark.trim()) return
    addRemark(record.id, {
      text: newRemark,
      createdAt: new Date().toISOString(),
      createdBy: "Admin",
    })
    setNewRemark("")
  }

  const handleUpdateRemark = (remarkId: string) => {
    if (!editingRemarkText.trim()) return
    updateRemark(record.id, remarkId, editingRemarkText)
    setEditingRemarkId(null)
    setEditingRemarkText("")
  }

  const handleDeleteRemark = (remarkId: string) => {
    deleteRemark(record.id, remarkId)
  }

  const startEditRemark = (remark: Remark) => {
    setEditingRemarkId(remark.id)
    setEditingRemarkText(remark.text)
  }

  return (
    <Dialog open={open} onOpenChange={onClose}>
      <DialogContent className="max-w-4xl bg-card border-border max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-4">
              <div className="flex h-12 w-12 items-center justify-center rounded-full bg-primary/20">
                <span className="text-lg font-semibold text-primary">
                  {record.name
                    .split(" ")
                    .map((n) => n[0])
                    .join("")
                    .substring(0, 2)}
                </span>
              </div>
              <div>
                <DialogTitle className="text-xl font-semibold text-foreground">{record.name}</DialogTitle>
                <div className="flex items-center gap-2 mt-1">
                  <Badge variant="outline" className={cn("text-xs", statusStyles[record.status])}>
                    {record.status.replace("-", " ")}
                  </Badge>
                  <Badge
                    variant="outline"
                    className={
                      isProtect
                        ? "bg-blue-500/10 text-blue-500 border-blue-500/20 text-xs"
                        : "bg-green-500/10 text-green-500 border-green-500/20 text-xs"
                    }
                  >
                    {record.type}
                  </Badge>
                  <Badge variant="outline" className="text-xs capitalize">
                    {record.partner}
                  </Badge>
                </div>
              </div>
            </div>
          </div>
        </DialogHeader>

        {/* Status & Stage Controls */}
        <div className="flex items-center gap-4 py-4 border-b border-border">
          <div className="flex items-center gap-2">
            <span className="text-sm text-muted-foreground">Status:</span>
            <Select value={record.status} onValueChange={(v) => updateRecordStatus(record.id, v as Status)}>
              <SelectTrigger className="w-[140px] h-8 text-sm">
                <SelectValue />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="new">New</SelectItem>
                <SelectItem value="in-progress">In Progress</SelectItem>
                <SelectItem value="follow-up">Follow-up</SelectItem>
                <SelectItem value="closed">Closed</SelectItem>
              </SelectContent>
            </Select>
          </div>
          <div className="flex items-center gap-2">
            <span className="text-sm text-muted-foreground">Stage:</span>
            <Select value={record.stage} onValueChange={(v) => updateRecordStage(record.id, v)}>
              <SelectTrigger className="w-[160px] h-8 text-sm">
                <SelectValue />
              </SelectTrigger>
              <SelectContent>
                {stages.map((stage) => (
                  <SelectItem key={stage} value={stage}>
                    {stage}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
        </div>

        <Tabs defaultValue="details" className="mt-4">
          <TabsList className="bg-muted/50">
            <TabsTrigger value="details">Details</TabsTrigger>
            <TabsTrigger value="remarks">Remarks ({record.remarks.length})</TabsTrigger>
            <TabsTrigger value="activity">Activity Log</TabsTrigger>
          </TabsList>

          <TabsContent value="details" className="space-y-4 mt-4">
            {/* Contact & Basic Info */}
            <div className="grid grid-cols-2 gap-4">
              <Card className="bg-muted/20 border-border">
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium text-foreground flex items-center gap-2">
                    <User className="h-4 w-4" /> Contact Information
                  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-2">
                  <div className="flex items-center gap-2">
                    <Phone className="h-4 w-4 text-muted-foreground" />
                    <span className="text-sm font-mono">{record.mobileNumber}</span>
                  </div>
                  <div className="flex items-center gap-2">
                    <Building className="h-4 w-4 text-muted-foreground" />
                    <span className="text-sm capitalize">{record.partner}</span>
                  </div>
                  <div className="flex items-center gap-2">
                    <Calendar className="h-4 w-4 text-muted-foreground" />
                    <span className="text-sm">Form Filled: {record.formFilledDate}</span>
                  </div>
                </CardContent>
              </Card>

              <Card className="bg-muted/20 border-border">
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium text-foreground flex items-center gap-2">
                    <FileText className="h-4 w-4" /> Upload Info
                  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-2">
                  <p className="text-sm">
                    <span className="text-muted-foreground">Source:</span> {record.uploadedFrom}
                  </p>
                  <p className="text-sm">
                    <span className="text-muted-foreground">Uploaded:</span>{" "}
                    {new Date(record.uploadedAt).toLocaleString()}
                  </p>
                </CardContent>
              </Card>
            </div>

            {/* Type-specific details */}
            {isProtect ? (
              <Card className="bg-muted/20 border-border">
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium text-foreground">Protect Details</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="grid grid-cols-3 gap-4">
                    <div>
                      <p className="text-xs text-muted-foreground">Plan</p>
                      <p className="text-sm font-medium">{protectRecord.plan}</p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground">Institution</p>
                      <p className="text-sm font-medium">{protectRecord.institution}</p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground">Account Number</p>
                      <p className="text-sm font-medium font-mono">{protectRecord.accountNumber}</p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground">Account Type</p>
                      <p className="text-sm font-medium">{protectRecord.accountType}</p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground">Date Opened</p>
                      <p className="text-sm font-medium">{protectRecord.dateOpened}</p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground">EMI Date</p>
                      <p className="text-sm font-medium">{protectRecord.emiDate}</p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground">EMI Amount</p>
                      <p className="text-sm font-medium font-mono">{protectRecord.emiAmount.toLocaleString()}</p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground">DPD</p>
                      <p className="text-sm font-medium">{protectRecord.dpd} days</p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground">Current DPD</p>
                      <p className="text-sm font-medium">{protectRecord.currentDpd} days</p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground">DPD Group</p>
                      <Badge variant="outline">{protectRecord.dpdGroup}</Badge>
                    </div>
                  </div>
                </CardContent>
              </Card>
            ) : (
              <Card className="bg-muted/20 border-border">
                <CardHeader className="pb-2">
                  <CardTitle className="text-sm font-medium text-foreground">Settlement Details</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="grid grid-cols-3 gap-4">
                    <div>
                      <p className="text-xs text-muted-foreground">Lender Name</p>
                      <p className="text-sm font-medium">{settlementRecord.lenderName}</p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground">Credit Card No</p>
                      <p className="text-sm font-medium font-mono">{settlementRecord.creditCardNo || "N/A"}</p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground">Loan Acc No</p>
                      <p className="text-sm font-medium font-mono">{settlementRecord.loanAccNo}</p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground">Due Amount</p>
                      <p className="text-sm font-medium font-mono">{settlementRecord.dueAmt.toLocaleString()}</p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground">Due Date</p>
                      <p className="text-sm font-medium">{settlementRecord.dueDate}</p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground">Employment</p>
                      <p className="text-sm font-medium">{settlementRecord.currEmployment}</p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground">EMI Bounced</p>
                      <Badge variant={settlementRecord.isEmiBounced ? "destructive" : "outline"}>
                        {settlementRecord.isEmiBounced ? "Yes" : "No"}
                      </Badge>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground">Legal Notice</p>
                      <Badge variant={settlementRecord.isLegalNotice ? "destructive" : "outline"}>
                        {settlementRecord.isLegalNotice ? "Yes" : "No"}
                      </Badge>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground">Recommended Amount</p>
                      <p className="text-sm font-medium font-mono text-green-500">
                        {settlementRecord.recommendedAmt.toLocaleString()}
                      </p>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground">Customer Wish Amount</p>
                      <p className="text-sm font-medium font-mono">
                        {settlementRecord.customerWishAmt.toLocaleString()}
                      </p>
                    </div>
                  </div>
                </CardContent>
              </Card>
            )}
          </TabsContent>

          <TabsContent value="remarks" className="mt-4">
            <Card className="bg-muted/20 border-border">
              <CardHeader className="pb-2">
                <CardTitle className="text-sm font-medium text-foreground flex items-center gap-2">
                  <MessageSquare className="h-4 w-4" /> Remarks
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                {/* Add Remark */}
                <div className="flex gap-2">
                  <Textarea
                    placeholder="Add a remark..."
                    value={newRemark}
                    onChange={(e) => setNewRemark(e.target.value)}
                    className="min-h-[80px] bg-background"
                  />
                  <Button onClick={handleAddRemark} disabled={!newRemark.trim()} className="shrink-0">
                    <Plus className="h-4 w-4 mr-1" /> Add
                  </Button>
                </div>

                {/* Remarks List */}
                <div className="space-y-3">
                  {record.remarks.length === 0 ? (
                    <p className="text-sm text-muted-foreground text-center py-4">No remarks yet</p>
                  ) : (
                    record.remarks.map((remark) => (
                      <div key={remark.id} className="rounded-lg bg-background p-3 border border-border">
                        {editingRemarkId === remark.id ? (
                          <div className="space-y-2">
                            <Textarea
                              value={editingRemarkText}
                              onChange={(e) => setEditingRemarkText(e.target.value)}
                              className="min-h-[60px]"
                            />
                            <div className="flex gap-2">
                              <Button size="sm" onClick={() => handleUpdateRemark(remark.id)}>
                                <Save className="h-3 w-3 mr-1" /> Save
                              </Button>
                              <Button size="sm" variant="outline" onClick={() => setEditingRemarkId(null)}>
                                <X className="h-3 w-3 mr-1" /> Cancel
                              </Button>
                            </div>
                          </div>
                        ) : (
                          <>
                            <p className="text-sm text-foreground">{remark.text}</p>
                            <div className="flex items-center justify-between mt-2">
                              <p className="text-xs text-muted-foreground">
                                {remark.createdBy}  {new Date(remark.createdAt).toLocaleString()}
                                {remark.updatedAt && " (edited)"}
                              </p>
                              <div className="flex gap-1">
                                <Button
                                  size="icon"
                                  variant="ghost"
                                  className="h-6 w-6"
                                  onClick={() => startEditRemark(remark)}
                                >
                                  <Edit2 className="h-3 w-3" />
                                </Button>
                                <Button
                                  size="icon"
                                  variant="ghost"
                                  className="h-6 w-6 text-destructive"
                                  onClick={() => handleDeleteRemark(remark.id)}
                                >
                                  <Trash2 className="h-3 w-3" />
                                </Button>
                              </div>
                            </div>
                          </>
                        )}
                      </div>
                    ))
                  )}
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="activity" className="mt-4">
            <Card className="bg-muted/20 border-border">
              <CardHeader className="pb-2">
                <CardTitle className="text-sm font-medium text-foreground flex items-center gap-2">
                  <History className="h-4 w-4" /> Activity Timeline
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="relative">
                  <div className="absolute left-3 top-0 bottom-0 w-px bg-border" />
                  <div className="space-y-4">
                    {record.activityLog.length === 0 ? (
                      <p className="text-sm text-muted-foreground text-center py-4">No activity yet</p>
                    ) : (
                      record.activityLog
                        .slice()
                        .reverse()
                        .map((entry) => (
                          <div key={entry.id} className="relative pl-8">
                            <div className="absolute left-0 top-1.5 w-6 h-6 rounded-full bg-primary/20 flex items-center justify-center">
                              <div className="w-2 h-2 rounded-full bg-primary" />
                            </div>
                            <div className="rounded-lg bg-background p-3 border border-border">
                              <p className="text-sm font-medium text-foreground">{entry.action}</p>
                              <p className="text-xs text-muted-foreground mt-1">{entry.details}</p>
                              <p className="text-xs text-muted-foreground mt-1">
                                {entry.user}  {new Date(entry.timestamp).toLocaleString()}
                              </p>
                            </div>
                          </div>
                        ))
                    )}
                  </div>
                </div>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </DialogContent>
    </Dialog>
  )
}
