"use client"

import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Search, Download, X } from "lucide-react"
import { useCRMStore } from "@/lib/store"
import { cn } from "@/lib/utils"

export function FilterBar() {
    const {
        searchQuery,
        setSearchQuery,
        typeFilter,
        setTypeFilter,
        dateFilterType,
        setDateFilterType,
        setDateRange,
    } = useCRMStore()

    const dateFilters = [
        { label: "Today", value: "today" },
        { label: "Past 7 days", value: "7d" },
        { label: "Past 30 days", value: "30d" },
        { label: "Past 1 year", value: "1y" },
        { label: "Custom", value: "custom" },
    ] as const

    const handleDateFilterChange = (value: typeof dateFilterType) => {
        setDateFilterType(value)
        // Logic to set actual date range would go here based on value
        // For now we just set the type
        if (value === "custom") {
            // Open date picker logic
        } else {
            // Calculate dates
        }
    }

    return (
        <div className="flex flex-col gap-4 md:flex-row md:items-center md:justify-between">
            <div className="flex flex-wrap items-center gap-2">
                {dateFilters.map((filter) => (
                    <Button
                        key={filter.value}
                        variant={dateFilterType === filter.value ? "default" : "outline"}
                        size="sm"
                        onClick={() => handleDateFilterChange(filter.value)}
                        className="rounded-full h-8"
                    >
                        {filter.label}
                        {dateFilterType === filter.value && <X className="ml-2 h-3 w-3" />}
                    </Button>
                ))}
            </div>

            <div className="flex items-center gap-2 w-full md:w-auto">
                <div className="relative flex-1 md:w-64">
                    <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-muted-foreground" />
                    <Input
                        placeholder="Search records..."
                        value={searchQuery}
                        onChange={(e) => setSearchQuery(e.target.value)}
                        className="pl-8 h-9"
                    />
                </div>

                <div className="flex items-center border rounded-md overflow-hidden">
                    <Button
                        variant={typeFilter === "protect" ? "secondary" : "ghost"}
                        size="sm"
                        className="rounded-none h-9 px-3"
                        onClick={() => setTypeFilter("protect")}
                    >
                        Protect
                    </Button>
                    <div className="w-[1px] h-4 bg-border" />
                    <Button
                        variant={typeFilter === "settlement" ? "secondary" : "ghost"}
                        size="sm"
                        className="rounded-none h-9 px-3"
                        onClick={() => setTypeFilter("settlement")}
                    >
                        Settlement
                    </Button>
                    <div className="w-[1px] h-4 bg-border" />
                    <Button
                        variant={typeFilter === "both" ? "secondary" : "ghost"}
                        size="sm"
                        className="rounded-none h-9 px-3"
                        onClick={() => setTypeFilter("both")}
                    >
                        Both
                    </Button>
                </div>

                <Button variant="outline" size="icon" className="h-9 w-9">
                    <Download className="h-4 w-4" />
                </Button>
            </div>
        </div>
    )
}
