"use client"

import { useEffect, useState } from "react"
import { Sidebar } from "@/components/sidebar"
import { Header } from "@/components/header"
import { MetricsRow } from "@/components/dashboard/metrics-row"
import { FilterBar } from "@/components/dashboard/filter-bar"
import { RecentActivityTable } from "@/components/dashboard/recent-activity-table"
import { RecordDetailModal } from "@/components/record-detail-modal"
import { useCRMStore } from "@/lib/store"
import { sampleProtectRecords, sampleSettlementRecords, calculateDashboardStats } from "@/lib/sample-data"
import type { CRMRecord } from "@/lib/types"

export default function DashboardPage() {
  const { records, uploadHistory, setRecords } = useCRMStore()
  const [isHydrated, setIsHydrated] = useState(false)
  const [selectedRecord, setSelectedRecord] = useState<CRMRecord | null>(null)
  const [isModalOpen, setIsModalOpen] = useState(false)

  // Initialize with sample data if empty
  useEffect(() => {
    setIsHydrated(true)
    if (records.length === 0) {
      setRecords([...sampleProtectRecords, ...sampleSettlementRecords])
    }
  }, [records.length, setRecords])

  const stats = calculateDashboardStats(records, uploadHistory)

  const handleViewRecord = (record: CRMRecord) => {
    setSelectedRecord(record)
    setIsModalOpen(true)
  }

  if (!isHydrated) {
    return (
      <div className="min-h-screen bg-background flex items-center justify-center">
        <div className="animate-pulse text-muted-foreground">Loading...</div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-background">
      <Sidebar />
      <main className="ml-64">
        <Header title="Dashboard" subtitle="Overview of your loan settlement operations" />

        <div className="p-6 space-y-8">
          {/* Filters */}
          <section>
            <FilterBar />
          </section>

          {/* Metrics */}
          <section>
            <MetricsRow stats={stats} />
          </section>

          {/* Recent Activity */}
          <section>
            <RecentActivityTable
              records={records}
              onView={handleViewRecord}
            />
          </section>
        </div>
      </main>

      <RecordDetailModal
        record={selectedRecord}
        open={isModalOpen}
        onClose={() => setIsModalOpen(false)}
      />
    </div>
  )
}
