"use client"

import { Card, CardContent } from "@/components/ui/card"
import { FileText, Shield, Handshake, TrendingUp, TrendingDown, Users } from "lucide-react"
import { cn } from "@/lib/utils"

interface MetricTileProps {
    title: string
    value: number | string
    subtitle?: string
    icon: React.ElementType
    className?: string
    iconClassName?: string
    trend?: {
        value: number
        isPositive: boolean
    }
}

function MetricTile({ title, value, subtitle, icon: Icon, className, iconClassName, trend }: MetricTileProps) {
    return (
        <Card className={cn("overflow-hidden shadow-sm", className)}>
            <CardContent className="p-4 flex items-center justify-between">
                <div className="space-y-1">
                    <p className="text-sm font-medium text-muted-foreground">{title}</p>
                    <div className="flex items-baseline gap-2">
                        <h2 className="text-2xl font-bold">{value}</h2>
                        {trend && (
                            <div className={cn("flex items-center text-xs font-medium", trend.isPositive ? "text-green-600" : "text-red-600")}>
                                {trend.isPositive ? <TrendingUp className="h-3 w-3 mr-1" /> : <TrendingDown className="h-3 w-3 mr-1" />}
                                {trend.value}%
                            </div>
                        )}
                    </div>
                    {subtitle && <p className="text-xs text-muted-foreground">{subtitle}</p>}
                </div>
                <div className={cn("p-2 rounded-full bg-primary/10", iconClassName)}>
                    <Icon className="h-4 w-4" />
                </div>
            </CardContent>
        </Card>
    )
}

interface MetricsRowProps {
    stats: {
        totalRequests: number
        newRequestsToday: number
        totalProtect: number
        totalSettlement: number
        sayyamProtect: number
        snapmintProtect: number
        sayyamSettlement: number
        snapmintSettlement: number
    }
}

export function MetricsRow({ stats }: MetricsRowProps) {
    return (
        <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-6 gap-4">
            {/* Total Requests */}
            <MetricTile
                title="Total Requests"
                value={stats.totalRequests}
                subtitle="All time"
                icon={FileText}
                iconClassName="text-primary bg-primary/10"
                trend={{ value: 12, isPositive: true }}
            />

            {/* New Today */}
            <MetricTile
                title="New Today"
                value={stats.newRequestsToday}
                subtitle="Requests"
                icon={TrendingUp}
                iconClassName="text-green-500 bg-green-500/10"
            />

            {/* Total Protect */}
            <MetricTile
                title="Total Protect"
                value={stats.totalProtect}
                icon={Shield}
                iconClassName="text-blue-500 bg-blue-500/10"
                trend={{ value: 5, isPositive: true }}
            />

            {/* Total Settlement */}
            <MetricTile
                title="Total Settlement"
                value={stats.totalSettlement}
                icon={Handshake}
                iconClassName="text-orange-500 bg-orange-500/10"
                trend={{ value: 2, isPositive: false }}
            />

            {/* Protect Breakdown */}
            <Card className="overflow-hidden shadow-sm col-span-1">
                <CardContent className="p-4 space-y-3">
                    <div className="flex items-center justify-between">
                        <span className="text-sm font-medium text-muted-foreground">Protect Split</span>
                        <Shield className="h-4 w-4 text-blue-500" />
                    </div>
                    <div className="grid grid-cols-2 gap-2">
                        <div>
                            <div className="text-xs text-muted-foreground">Sayyam</div>
                            <div className="text-lg font-bold">{stats.sayyamProtect}</div>
                        </div>
                        <div>
                            <div className="text-xs text-muted-foreground">Snapmint</div>
                            <div className="text-lg font-bold">{stats.snapmintProtect}</div>
                        </div>
                    </div>
                </CardContent>
            </Card>

            {/* Settlement Breakdown */}
            <Card className="overflow-hidden shadow-sm col-span-1">
                <CardContent className="p-4 space-y-3">
                    <div className="flex items-center justify-between">
                        <span className="text-sm font-medium text-muted-foreground">Settle Split</span>
                        <Handshake className="h-4 w-4 text-orange-500" />
                    </div>
                    <div className="grid grid-cols-2 gap-2">
                        <div>
                            <div className="text-xs text-muted-foreground">Sayyam</div>
                            <div className="text-lg font-bold">{stats.sayyamSettlement}</div>
                        </div>
                        <div>
                            <div className="text-xs text-muted-foreground">Snapmint</div>
                            <div className="text-lg font-bold">{stats.snapmintSettlement}</div>
                        </div>
                    </div>
                </CardContent>
            </Card>
        </div>
    )
}
